    box-sizing: border-box;
} */

body {
    font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen,
        Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
    background-color: #fafafa;
    color: #212121;
    line-height: 1.5;
}

ul {
    margin: 0;
    padding: 0;
    list-style: none;
}

img {
    max-width: 100%;
    height: auto;
    display: block;
}

h1,
h2,
h3,
h4,
h5,
h6,
p {
    margin: 0;
    padding: 0;
}

#search-input {
    font: inherit;
    padding: 8px;
    max-width: 320px;
    margin-bottom: 16px;
}

.country-card__wrapper {
    display: flex;
    align-items: center;
    margin-bottom: 30px;
}

.country-card__flags {
    margin-right: 10px;
}

.country-card__name {
    font-size: 30px;
}

.country-card__capital {
    margin-bottom: 10px;
}

.country-card__population {
    margin-bottom: 10px;
}

.country-card__text {
    font-weight: 700;
}

.country-list__item {
    display: flex;
    align-items: center;
    margin-bottom: 5px;
}

.country-list__flags {
    margin-right: 10px;
}

.country-list__name {
    font-size: 18px;
    font-weight: 500;
    margin-left: 10px;
}